.Log {
    background-color: rgb(219, 144, 31);
    width: 100%;
    height: 40px;
    position: -webkit-sticky;
    position: fixed!important;
  }
  
  /* button {
    /* float: right; */
    /* margin-right: 20px;
    margin-top:-85px; 
  } */
  table td{
    cursor: pointer;
  }
  
  .pro-sidebar {
     /* height: 100vh!important; */
     color: #adadad;
     height: 100%;
     width: 230px;
     min-width: 230px;
     text-align: left;
     transition: width, left, right, 0.3s;
     position: relative;
     z-index: 1009;
     margin-top: 140px;
     margin-bottom: 140px!important;
  }
  .pro-sidebar-inner{
      position: fixed!important;
      width: 230px;
      min-width: 230px;
  }
  .pro-sidebar .pro-menu {
      padding-top: 10px;
      padding-bottom: 10px;
      margin-bottom: 140px;
  }
  
  .activeClass{
    position: relative;
    color: black !important;
}

.activeClass::before{
  content: '';
  width:95%;
  height: 100%;
  position: absolute;
  background-color: #f1f6f9 ;
  animation: backgroundColor 0.5s ease;
  -webkit-animation: backgroundColor 0.5s ease;
}


@keyframes backgroundColor {
    0%{
        opacity: 0;
    } 
    100%{
      opacity: 1;
    }
} 